import React, { ReactElement, useRef } from "react";
import { Stack, Text, Flex, Box, ChakraProvider } from "@chakra-ui/react";

import "./marketing.css";
import MarketingFooter from "./MarketingFooter";
import { Header } from "./Header";
import { Body } from "./Body";
import { FeaturesList } from "./FeaturesList";
import { defaultTheme } from "../theme/chakraTheme";

interface FeatureProps {
  text: string;
  iconBg: string;
  icon?: ReactElement;
}

export const Feature = ({ text, icon, iconBg }: FeatureProps) => {
  return (
    <Stack direction={"row"} align={"center"}>
      <Flex
        w={8}
        h={8}
        align={"center"}
        justify={"center"}
        rounded={"full"}
        bg={iconBg}
      >
        {icon}
      </Flex>
      <Text fontWeight={600}>{text}</Text>
    </Stack>
  );
};

const MarketingPage: React.FunctionComponent<any> = (props) => {
  let moreInfoRef = useRef<HTMLDivElement>(null);

  return (
    <ChakraProvider theme={defaultTheme}>
      <Box overflowY="auto">
        <Header />
        <Body moreInfoRef={moreInfoRef} />
        <FeaturesList />
        <MarketingFooter />
      </Box>
    </ChakraProvider>
  );
};

export default MarketingPage;
